'use client';
import { useEffect, useState } from 'react';
import { createClientComponentClient } from '@supabase/auth-helpers-nextjs';

export default function IntelFeed() {
  const supabase = createClientComponentClient();
  const [intel, setIntel] = useState<any[]>([]);
  const [filterSentiment, setFilterSentiment] = useState('All');

  useEffect(() => {
    fetchIntel();
    const channel = supabase
      .channel('intel_feed_changes')
      .on('postgres_changes', { event: '*', schema: 'public', table: 'intel_feed' }, fetchIntel)
      .subscribe();

    return () => supabase.removeChannel(channel);
  }, [filterSentiment]);

  const fetchIntel = async () => {
    let query = supabase.from('intel_feed').select('*').order('id', { ascending: false }).limit(20);
    if (filterSentiment !== 'All') query = query.eq('sentiment_level', filterSentiment);
    const { data, error } = await query;
    if (!error && data) setIntel(data);
  };

  return (
    <div className="p-4 bg-slate-900 text-white rounded-xl w-full max-w-lg mx-auto mt-4">
      <div className="flex justify-between items-center mb-2">
        <h2 className="text-lg font-semibold">A.I.V.E. Live Feed</h2>
        <select
          value={filterSentiment}
          onChange={(e) => setFilterSentiment(e.target.value)}
          className="bg-slate-800 border border-slate-700 rounded p-1 text-sm"
        >
          <option>All</option>
          <option>Positive</option>
          <option>Neutral</option>
          <option>Negative</option>
        </select>
      </div>

      {intel.map((item) => (
        <div key={item.id} className="p-3 mb-2 bg-slate-800 rounded-lg">
          <h3 className="font-semibold">{item.headline}</h3>
          <p className="text-sm text-slate-300">{item.summary}</p>
          {item.insights && (
            <p className="text-xs text-amber-300 mt-1 italic">‚Äú{item.insights}‚Äù</p>
          )}
          <div className="flex justify-between text-xs text-slate-400 mt-2">
            <span>üåé {item.region}</span>
            <span>üß† {item.sentiment_level}</span>
            <span>üìä {item.impact_score}</span>
          </div>
        </div>
      ))}
    </div>
  );
}
