'use client';
import { useState } from 'react';

export default function SubmitIntel() {
  const [headline, setHeadline] = useState('');
  const [summary, setSummary] = useState('');
  const [region, setRegion] = useState('USA');
  const [sentiment, setSentiment] = useState('Neutral');
  const [impact, setImpact] = useState(50);
  const [loading, setLoading] = useState(false);
  const [response, setResponse] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setResponse('Analyzing with A.I.V.E...');

    try {
      const res = await fetch(process.env.NEXT_PUBLIC_AIVE_FUNCTION_URL!, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ headline, summary, region, sentiment, impact }),
      });

      const data = await res.json();
      const insights = data?.insights ?? 'No insights generated.';

      // Insert directly into Supabase table
      const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!;
      const supabaseKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;
      const { createClient } = await import('@supabase/supabase-js');
      const supabase = createClient(supabaseUrl, supabaseKey);

      const { error } = await supabase.from('intel_feed').insert([
        { headline, summary, region, sentiment_level: sentiment, impact_score: impact, insights },
      ]);

      if (error) throw error;
      setResponse('‚úÖ Intel submitted successfully.');
      setHeadline('');
      setSummary('');
    } catch (err: any) {
      console.error('‚ùå A.I.V.E. failed:', err);
      setResponse('‚ùå Failed to submit intel.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-4 bg-slate-900 rounded-xl shadow-md text-white w-full max-w-md mx-auto">
      <h3 className="text-lg font-semibold mb-3">üöÄ Submit Intel</h3>
      <form onSubmit={handleSubmit} className="flex flex-col gap-2">
        <input
          type="text"
          placeholder="Headline"
          className="p-2 rounded bg-slate-800 border border-slate-700"
          value={headline}
          onChange={(e) => setHeadline(e.target.value)}
          required
        />
        <textarea
          placeholder="Summary"
          className="p-2 rounded bg-slate-800 border border-slate-700"
          value={summary}
          onChange={(e) => setSummary(e.target.value)}
        />
        <input
          type="text"
          placeholder="Region"
          className="p-2 rounded bg-slate-800 border border-slate-700"
          value={region}
          onChange={(e) => setRegion(e.target.value)}
        />
        <select
          value={sentiment}
          onChange={(e) => setSentiment(e.target.value)}
          className="p-2 rounded bg-slate-800 border border-slate-700"
        >
          <option>Positive</option>
          <option>Neutral</option>
          <option>Negative</option>
        </select>
        <input
          type="number"
          placeholder="Impact (1-100)"
          className="p-2 rounded bg-slate-800 border border-slate-700"
          value={impact}
          onChange={(e) => setImpact(Number(e.target.value))}
        />
        <button
          type="submit"
          disabled={loading}
          className="mt-2 p-2 bg-blue-600 rounded hover:bg-blue-500"
        >
          {loading ? 'Processing‚Ä¶' : 'Submit to A.I.V.E.'}
        </button>
      </form>
      <p className="text-sm text-slate-400 mt-2">{response}</p>
    </div>
  );
}

